*{
  padding: 0;
  margin: 0;
  list-style-type: none;
  text-decoration: none;
  box-sizing: border-box;
}
p{
  color: white;
}
body{
  background-image: url("image/ahmed.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover; 
    background-color: rgb(31, 31, 32); 
  background-blend-mode: overlay;
  /* height:200vh;  */
  width: 100%;
}
header{

  /* background-color: rgb(219, 10, 10); */
  padding: 20px;
}
.ahmed{
  /* border: 2px solid green; */
  color: white;

}
header nav{
    display: flex;
  justify-content: space-between;
  margin-left: 20px;
  /* background-color: yellow; */
}

header nav ul{
display: flex;
justify-content: space-between;
  /* border: 2px solid rgb(109, 128, 0); */


}

header nav ul li a {
  color: green;
  padding: 20px;
}
.About{
  padding: 70px;
}
.About p{
font-size: 100px;
position: relative;

}
.About p::before{
  position: absolute;
  content: "";
  width: 50px;
  height: 5px;
  top: 10px;
  background-color: red;
}
button{
  border:2px solid red;
  padding: 10px;
  background-color: black;
  color: white;
}
.footer{
  display: flex;
  justify-content: space-around;
  
  /* height: 400px; */
  /* margin-top: 20%; */

}
.footer div{
  flex-basis: 30%;
  color: white;
  padding: 40px;


}
.footer div h1{
  position: relative;

}
.footer div:first-child{
  background-color: rgb(252, 8, 8);
  height: 100%;
  
}
.footer div h1:before{
 position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  bottom: -4px;
  background-color: white;
}
.footer div:nth-child(2){
  background-color: rgb(37, 37, 37);
  height: 100%;
}

.footer div:last-child{
 background-color: rgb(37, 37, 37);
  height: 100%;
}
.footer p{
  margin-top: 10px;
}
.Nimcaan h1{
  color: white;
}
.todo{
  width: 400px;
  background-color: white;
}
table,th,td{
border: 2px solid green;
border-collapse: collapse;
}























































